body.register-page {
  background: linear-gradient(90deg, #e6f9ff 50%, rgba(255, 255, 255, 0) 100%);
}

body.register-page .container > .row {
  min-height: 100vh;
  padding-top: 70px;
  padding-bottom: 200px;
}

.register-page--header {
  min-height: 30vh;
}

.register-page--header:before {
  content: "";
  position: absolute;
  width: 800px;
  max-width: 50vw;
  height: 450px;
  background-image: url(../img/login-page--triangles.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.form-row--header {
  padding: 5%;
  background: linear-gradient(-180deg, rgba(114, 47, 47, 0) 0%, white 100%);
  margin-bottom: 4%;
}

.form-row--header span {
  margin-top: 10%;
  text-transform: capitalize;
  font-family: Roboto Condensed, sans-serif;
  font-size: 1.4rem;
}
